import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'main',
      component: () => import('@/components/main'),
    },
    {
      path: '/admin',
      name: 'admin',
      meta:{requireAuth:true},
      component: () => import('@/components/admin'),
    },
    {
      path: '/publish',
      name: 'publish',
      meta:{requireAuth:true},
      component: () => import('@/components/publish'),
    },
  ]
})




